import React, { Component } from 'react';
import './uiControls.less';
import Toggle from './toggle';
import AnalogWriteSlider from './analogWriteSlider';
import AnalogReadButton from './analogReadButton';
import Dial from "./dial";
import { Row, Button, Icon } from "antd";

class UIcontrols extends Component {
    constructor() {
        super();
        this.state = {
            capture: false,
        };
    }

    onCaptureClick() {
        const { getCapture } = this.props;
        let { capture } = this.state;
        capture = !capture;
        this.setState({ capture: capture });
        getCapture(capture);
        if (capture) {
            fetch('/rotate/0/0/' + this.state.activeUnit + '/')
                .then(res => res.json()).then(data => {
                    // console.log(data);
                });
        }
    }

    render() {
        const { activeUnit, colors, allUnits, steps, activePointVal, getDialPos, screen } = this.props;
        const { capture } = this.state;
        const captureIcon = capture ? "vertical-align-top" : "vertical-align-bottom";
        const activeColor = colors[allUnits.indexOf(activeUnit)];
        let dialRadius = 100;
        let componentSize = "default"
        if (screen.width < 400) {
            dialRadius = 50;
            componentSize = "small"
        }
        else if (screen.width < 600) {
            dialRadius = 75;
            componentSize = "small"
        }
        else if (screen.width > 1080) {
            dialRadius = 100;
            componentSize = "default"
        }
        return (
            <div className="UIcontrols">
                <div className="UIpinBox">
                    <Row type="flex" justify="start" align="middle" >
                        <div className="UIpinUnit">
                            p11 <Toggle unitId={activeUnit} pin={11} size={componentSize} />
                        </div>
                    </Row>
                    <Row type="flex" justify="start" align="middle" >
                        <div className="UIpinUnit">
                            p12 <Toggle unitId={activeUnit} pin={12} size={componentSize} />
                        </div>
                    </Row>
                    <Row type="flex" justify="start" align="middle" >
                        <div className="UIpinUnit">
                            p13 <Toggle unitId={activeUnit} pin={13} size={componentSize} />
                        </div>
                    </Row>
                    <Row type="flex" justify="start" align="middle" >
                        <div className="UIpinUnit">
                            a2 <AnalogReadButton unitId={activeUnit} pin={16} size={componentSize} />
                        </div>
                    </Row>
                    <Row type="flex" justify="start" align="middle" >
                        <div className="UIpinUnit">
                            a3 <AnalogReadButton unitId={activeUnit} pin={17} size={componentSize} />
                        </div>
                    </Row>
                    <Row type="flex" justify="start" align="middle" >
                        <div className="UIpinUnit">
                            a4 <AnalogReadButton unitId={activeUnit} pin={18} size={componentSize} />
                        </div>
                    </Row>
                </div>
                <div className="UIvSliderBox" >
                    <div className="UIvSliderUnit">
                        <AnalogWriteSlider unitId={activeUnit} pin={14} />
                        <br />
                        <span className="UIvSliderTitle">a0</span>
                    </div>
                    <div className="UIvSliderUnit">
                        <AnalogWriteSlider unitId={activeUnit} pin={15} />
                        <br />
                        <span className="UIvSliderTitle">a1</span>
                    </div>
                    <div className="UIvSliderUnit">
                        <AnalogWriteSlider unitId={activeUnit} pin={10} />
                        <br />
                        <span className="UIvSliderTitle">p10</span>
                    </div>
                </div>
                <div className="UIdialBox">             
                    <Dial
                        unitId={activeUnit}
                        steps={steps}
                        radius={dialRadius}
                        speed={255}
                        capture={capture}
                        getDialPos={getDialPos}
                        incomingVal={activePointVal}
                        vscreen={screen}
                    />
                    <Button type="link"
                        onClick={this.onCaptureClick.bind(this)}
                        className="UIcaptureIconUnit"
                    >
                        <Icon
                            type={captureIcon}
                            className="UIcaptureButtonText"
                            style={{ color: activeColor }}
                        />
                    </Button>
                    <span className="UImotorId">MO:{activeUnit}</span>
                </div>
            </div>
        );
    }
}

export default UIcontrols;